import { ChipIcon } from "@heroicons/react/solid";
import React from "react";
import { skills } from "./data";

export default function Skills() {
  return (
    <section id="skills">
      <div className="container px-5 py-10 mx-auto">
        <div className="text-center mb-20">
          <ChipIcon className="w-10 inline-block mb-4" />
          <h1 className="sm:text-4xl text-3xl font-medium title-font text-white mb-4">
            Skills &amp; Technologies
          </h1>
          <p className="text-base leading-relaxed xl:w-2/4 lg:w-3/4 mx-auto">
          Explore my diverse set of skills and technologies that empower me to tackle challenges and create innovative solutions. With a passion for continuous learning, I stay adaptable to emerging trends in the ever-evolving world of technology.
          </p>
        </div>
        <div className="flex flex-wrap lg:w-4/5 sm:mx-auto sm:mb-2 -mx-2">
          {skills.map((skill) => (
            <div key={skill.language} className="p-2 sm:w-1/2 w-full">
              <div className="bg-gray-800 rounded flex p-4 h-full justify-center items-center">
                {/* <BadgeCheckIcon className="text-green-400 w-6 h-6 flex-shrink-0 mr-4" /> */}
                <img
                  src={skill.logo}
                  height={25}
                  width={25}
                  className="ml-4 mr-3"
                  alt="logo"
                />
                <span className="title-font font-medium text-white">
                  {skill.language}
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}


